@charset "UTF-8";
/**
 * Stylesheet: Main Stylesheet
 *
 */
@font-face {
  font-family: "icons";
  src: url("icons.eot");
  src: url("icons.eot?#iefix") format("eot"), url("icons.woff") format("woff"), url("icons.ttf") format("truetype"), url("icons.svg#icons") format("svg");
  font-weight: normal;
  font-style: normal;
}

/**
 * Mixin - Icon
 *
 * Icon stack:
 * 1. 00-config/_icons.scss (autogenerated)
 * 2. 10-base/_icons-base.scss (here)
 * 3. 30-global/_icons-application.scss (Generate default classes)
 */
/*
 * Main Icon mixin
 *
 * Can be called like @include icon($vars) { background: something; } using the
 * power of @content.
 */
/**
 * Font application to generic DOM
 */
[class*="icon--"] {
  font-family: "icons";
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  font-weight: normal;
  font-style: normal;
  speak: none;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: optimizeLegibility;
}

.icon--instagram:before {
  content: "";
}

.icon--linkedin:before {
  content: "";
}

.icon--mail:before {
  content: "";
}

.icon--menu:before {
  content: "";
}

.icon--twitter:before {
  content: "";
}

input:focus, [role="combobox"]:focus {
  outline: none;
  box-shadow: 0 0 0 4px rgba(0, 0, 255, 0.5);
}

.hidden {
  display: none;
  visibility: hidden;
}

/* hide some stuff temporarily */
* {
  box-sizing: border-box;
}

